import {useNavigate} from "react-router-dom";

const Header = () => {
    const navigate = useNavigate();
    const logout = () => {
        localStorage.clear()
        navigate('/login')
    }

    const goTodo = () => {
        navigate('/todo')
    }


    return (
        <div className="app-header">
            <div className="app-header-title">
                <h1></h1>
                <p onClick={goTodo}></p>
            </div>
            <div className="text" onClick={logout}></div>
        </div>
    )
}

export default Header